.progress-ring circle {
  /* clip-path: circle(60%); */
  animation: circle var(--duration) infinite linear;
}

svg text {
  /* opacity: 0; */
}

@keyframes circle {
  from {
    stroke-dashoffset: var(--circumference);
  }
  to {
    stroke-dashoffset: 0;
  }
}

.button {
  background: white;
  border: 1px solid grey;
  padding: 5px 10px;
  color: black;
  text-decoration: none;
}

input,
button {
  font-size: 100%;
}

.arrow {
  cursor: pointer;
}
